import { FC, memo } from 'react'

import { NavigationLink, NavigationLinkProps } from './components/navigationLink'
import { NavigationStyle } from './styles'

export interface INavigation {
  links?: NavigationLinkProps[]
}

export const Navigation: FC<INavigation> = memo((props) => {
  const { links, ...rest } = props

  if (!links) {
    return <></>
  }

  return (
    <NavigationStyle {...rest}>
      {links.map((pageProps: NavigationLinkProps) => (
        <NavigationLink key={pageProps.href} {...pageProps} />
      ))}
    </NavigationStyle>
  )
})

Navigation.displayName = 'Navigation'
