import React, { FC, PropsWithChildren, ReactNode } from 'react'
import {
  CookiesTooltip,
  CookieThemeProvider,
  ToastContainer,
  migrationThemeCookiesToCrossDomainCookiesClientSide,
} from '@lidofinance/lido-ui'

import { GlobalStyles } from './globalStyles'
import { Header, Main, Footer } from './layoutComponents'
import { NavigationLinkProps } from './layoutComponents/header/components/navigation/components/navigationLink'

// Migrations old cookies to new cross domain cookies
migrationThemeCookiesToCrossDomainCookiesClientSide()

export type WidgetAppProps = {
  headerActions: ReactNode
  navigation?: NavigationLinkProps[]
}

export const WidgetApp: FC<PropsWithChildren<WidgetAppProps>> = ({ ...props }) => {
  return (
    <CookieThemeProvider>
      <GlobalStyles />
      <Header headerActions={props.headerActions} navigation={props?.navigation} />
      <Main>{props.children}</Main>
      <Footer />
      <CookiesTooltip />
      <ToastContainer />
    </CookieThemeProvider>
  )
}

export type { NavigationLinkProps }
export { LayoutTitle, LayoutSubTitle } from './layoutComponents'
